import Reflux from 'reflux'
import ModalActions from '../actions/ModalActions.js'

let MapShortDescriptionStore = Reflux.createStore({

	listenables: [ModalActions],

	getInitialState() {
		return this.state;
	},

	state: {
		type: null
	},
	
	open(new_params) {
		this.state = new_params;
		this.trigger(this.state);
	},
	
	close() {
		this.state = {
			type: null
		};
		this.trigger(this.state);
	}
	
});

export default MapShortDescriptionStore;



