import React from 'react';
import Meniu from './Meniu.js'
import Modal from './Modal.js'
import Language from './Language.js'

let App = React.createClass({
  render() {
    return (
      <div id="App" className='container-fluid'>
      	<div className='row'>
      		<Language/>
					<Meniu/>
					<Modal/>
					{this.props.children}
				</div>
      </div>
    );
  }
});

export default App;



