import React from 'react'
import MapActions from '../actions/MapActions.js';

let Language = React.createClass({

	getInitialState() {
		window.$.ajax({
			method: "POST",
			url: window.apiurl,
			data: {
				url: '/languages'
			},
			success: data => {
				this.setState({
					lgv: data.languages
				});
			}
		});
		return {
			visible: true,
			lgv: []
		};
	},

	close(lg) {
		window.lgid = lg.id;
		MapActions.getData();
		this.setState({
			visible: false
		});
	},

	render() {
	
		if (!this.state.visible) {
			return null;
		}
		
		let buttons = this.state.lgv.map(lg => {
			return <button onClick={this.close.bind(this, lg)} className="btn btn-lg btn-inverse" href="#" role="button">
				{lg.name}
			</button>;
		});
		
		return <div id='language'>
			<div className="jumbotron">
				<h1>Choose your language</h1>
				<p></p>
				<p>
					{buttons}
				</p>
			</div>
		</div>
	}	
	
});

export default Language;




