import Reflux from 'reflux'
import MapActions from '../actions/MapActions.js'

let MapShortDescriptionStore = Reflux.createStore({

	listenables: [MapActions],

	getInitialState() {
		return this.state;
	},
	
	toggle(type) {
		this.state.filters[type] = !this.state.filters[type];
		for (let i in this.state.data) {
			this.state.data[i].visible = this.state.filters[this.state.data[i].type.id];
		}
		this.trigger(this.state);
	},
	
	getData() {
		window.$.ajax({
			method: "POST",
			url: window.apiurl,
			data: {
				url: '/objectives',
				"languageId": window.lgid,
				"filteredTypes": [
					0
				],
				"xLeftTop": 0,
				"yLeftTop": 0,
				"xRightBottom": 100,
				"yRightBottom": 100
			},
			success: data => {
				this.state.data = data.objectives;
				for (let i in this.state.data) {
					this.state.data[i].visible = true;
				}
				this.trigger(this.state);
			},
		});
	},

	state: {
		data:  [],
  	filters: [true, true, true, true, true, true, true, true, true, true, true]
	},
	
});

export default MapShortDescriptionStore;



