import Reflux from 'reflux'
import MapShortDescriptionActions from '../actions/MapShortDescriptionActions.js'

let MapShortDescriptionStore = Reflux.createStore({

	listenables: [MapShortDescriptionActions],

	getInitialState() {
		return this.state;
	},

	state: {
		ob: null,
		e: null
	},
	
	showInfo(ob, e) {
		this.state.ob = ob;
		this.state.e = e;
		this.trigger(this.state);
	},
	closeInfo() {
		this.state.ob = null;
		this.trigger(this.state);
	}
	
});

export default MapShortDescriptionStore;



