import React from 'react'
import Reflux from 'reflux'
import {Link, transitionTo, hashHistory} from 'react-router'
import ModalStore from '../stores/ModalStore.js';
import ModalActions from '../actions/ModalActions.js';
import MapActions from '../actions/MapActions.js';
import MapStore from '../stores/MapStore.js';

let Modal = React.createClass({

	mixins: [
		Reflux.connect(ModalStore, 'modalData')
	],	
	
	render() {
		if (!this.state) {
			return null;
		}
		if (!this.state.modalData) {
			return null;
		}
		if (!this.state.modalData.type) {
			return null;
		}
		
		if (this.state.modalData.type == 'list') {
			return <ListModal data={this.state.modalData} />;
		}
		
		if (this.state.modalData.type == 'objective') {
			return <ObjectiveModal data={this.state.modalData.objective} />;
		}
		
		return null;
	}

});

let ObjectiveModal = React.createClass({
	point_objective() {
		ModalActions.close();
		window.map.setCenter({
			lat: this.props.data.location.x,
			lng: this.props.data.location.y
		}); 
    window.map.setZoom(15);
	},
	
	getInitialState() {
	
		this.make_call();
	
		return {
			html: ''
		}
	},
	
	make_call() {
		window.$.ajax({
			method: 'POST',
			url: window.apiurl,
			data: {
				url: '/objectiveHtml',
				objectiveId: this.props.data.id
			},
			success: data => {
				this.setState({
					html: data.html
				});
			}
		});
	},

	render() {
	
		return <div id='modal'>
			<div className='x-button' onClick={ModalActions.close}>
				<span className='glyphicon glyphicon-remove'>
				</span>
			</div>
			<div className='container-fluid'>
				<div className='row'>
					<div className='col-xs-12'>
						<h2 style={{marginBottom: '15px'}}> {this.props.data.name}: </h2>
					</div>
					<div className='col-xs-12' dangerouslySetInnerHTML={{ __html: this.state.html }}>
					</div>					
				</div>
				<div className='row'>
				</div>
			</div>
		</div>;
	
	}
});

let ListModal = React.createClass({

	mixins: [
		Reflux.connect(MapStore, 'mapstore')
	],
	
	getInitialState() {
		return {
			filter: ''
		}
	},
	
	filterChange(e) {
		this.setState({
			filter: e.target.value
		});
	},

	render() {
		
		let objectives = this.state.mapstore.data.map(ob => {
			if (ob.type.id != this.props.data.params.id || !ob.name.toLowerCase().includes(this.state.filter)) {
				return null;
			}
			return <Objective ob={ob}/>;	
		});
	
		return <div id='modal'>
			<div className='x-button' onClick={ModalActions.close}>
				<span className='glyphicon glyphicon-remove'>
				</span>
			</div>
			<div className='container-fluid'>
				<div className='row'>
					<div className='col-xs-12'>
						<h2 style={{marginBottom: '15px'}}> {this.props.data.params.name}: </h2>
					</div>
				</div>
				<div className='row' style={{paddingBottom: '10px'}}>
					<div className='col-xs-12 col-sm-6 col-md-6 col-lg-3'>
						 <div className="input-group">
							<input type="text" className="form-control" placeholder="Search for..." onChange={this.filterChange}/>
							<span className="input-group-btn">
								<button className="btn" type="button">
									<span className='glyphicon glyphicon-search'></span>
								</button>
							</span>
						</div>
					</div>
				</div>
				<div className='row'>
					{objectives}
				</div>
			</div>
		</div>;
	}

});

let Objective = React.createClass({
	
	point_objective() {
		ModalActions.close();
		window.map.setCenter({
			lat: this.props.ob.location.x,
			lng: this.props.ob.location.y
		}); 
    window.map.setZoom(15);
	},

	render() {
		return <div className='modal-objective col-lg-4 col-md-6 col-sm-6 col-xs-12'>
			<div className='thumbnail'>
				<img src='http://image.stirileprotv.ro/media/images/600x375/Oct2010/60449612.jpg'/>
				<div className='caption'>
					<h3> { this.props.ob.name } </h3>
					<p> {this.props.ob.description}
					</p>
					<p>
						<button className='btn btn-primary' onClick={this.point_objective}>
							Arata pe harta
						</button>
					</p>
				</div>
			</div>
		</div>;
	}
});

export default Modal;




