import React from 'react'
import Reflux from 'reflux'
import {Link, transitionTo, hashHistory} from 'react-router'
import ModalActions from '../actions/ModalActions.js';
import MapActions from '../actions/MapActions.js';
import MapStore from '../stores/MapStore.js';

let data = [
    {
      "id": 1,
      "name": "Birouri imigrari",
      "iconClass": "fa fa-institution"
    },
    {
      "id": 2,
      "name": "Servicii socio-medicale",
      "iconClass": "fa fa-h-square"
    },
    {
      "id": 3,
      "name": "Servicii gratuite ONG",
      "iconClass": "fa fa-gg-circle"
    },
    {
      "id": 4,
      "name": "Locatii divertisment",
      "iconClass": "fa fa-universal-access"
    },
    {
      "id": 5,
      "name": "Atractii turistice",
      "iconClass": "fa fa-archive"
    },
    {
      "id": 6,
      "name": "Altele",
      "iconClass": "fa fa-bell-o"
    }
];

let Meniu = React.createClass({

	mixins: [Reflux.connect(MapStore, 'mapstore')],

	getInitialState() {
		let checked_data = data;
		for (let i in checked_data) {
			checked_data[i].checked = true;
		}
		return {
			types: data,
			visible: false
		};
	},
	
	componentDidUpdate() {
		if (window.meniuGot) {
			return;
		}
		window.meniuGot = true;
		window.$.ajax({
			method: 'POST',
			url: window.apiurl,
			data: {
				url: '/objectiveTypes',
				languageId: window.lgid
			},
			success: data => {
				let checked_data = data.objectiveTypes;
				for (let i in checked_data) {
					checked_data[i].checked = true;
				}
				this.setState({
					types: data.objectiveTypes
				});
			}
		});
	},
	
	toggle_filter(type) {
		MapActions.toggle(type.id);
	},
	
	resetMap() {
		window.map.setZoom(7);
		window.map.setCenter({
			lat: 44.439663,
		  lng: 26.096306
		});
	},
	
	toggleMenu() {
		this.setState({
			visible: !this.state.visible
		});
	},
	
	openModal(params) {
		this.setState({
			visible: false
		});
		ModalActions.open(params);
	},
	
	render() {
		let onPhone = window.screen.width < 768;
	
		let items = this.state.types.map(it => 
			<li onClick={this.openModal.bind(this, {type: 'list', params:it})}>
				<a href='#'>
					{it.name}
				</a>
			</li>
		);
		let cbs = this.state.types.map(it => 
			<li>
				<input onChange={this.toggle_filter.bind(this, it)} type="checkbox" defaultChecked={true}/>
				{it.name}
			</li>
		);
		
		if (onPhone && !this.state.visible) {
			return <div id='hamburger' onClick={this.toggleMenu}>
				<div className='glyphicon glyphicon-menu-hamburger'>
				</div>
			</div>;
		}
		
		let closeButton = null;
		if (onPhone) {
			closeButton = <div className='x-button' onClick={this.toggleMenu}>
				<span className='glyphicon glyphicon-remove'>
				</span>
			</div>;
		}
		
		let searchBar = <div className="input-group">
	    <input type="text" className="form-control form-control-inverse" placeholder="Search for..."/>
	    <span className="input-group-btn">
	      <button className="btn btn-inverse" type="button">
	      	<span className='glyphicon glyphicon-search'></span>
	      </button>
	    </span>
	  </div>;
		if (true) {
			searchBar = null;
		}
		
		return <div className='col-lg-2 col-md-3 col-sm-3' id='meniu'>
			{ closeButton }
		  { searchBar }
		  <h3>
		  	Filtreaza:
		  </h3>
		  
	  	<ul id='filter-ul'>
	  		{ cbs }
	  	</ul>
		  
			<h3>
				Listeaza:
			</h3>
			<ul className="nav nav-pills nav-stacked nav-inverse">
				{ items }
			</ul>
			
			<h4 style={{color:'rgba(255,255,255,.8)', marginTop: '30px', cursor: 'pointer'}}
				onClick={this.resetMap}> 
				<span className='glyphicon glyphicon-repeat' style={{marginRight: '10px'}}>
				</span>
				Reseteaza harta 
			</h4>
		</div>
	}

});

export default Meniu;



