import React from 'react';
import ReactDOM from 'react-dom';
import {Gmaps, Marker, InfoWindow, Circle} from 'react-gmaps';
import App from './App.js'
import MapStore from '../stores/MapStore.js';
import MapActions from '../actions/MapActions.js';
import ModalActions from '../actions/ModalActions.js';
import Reflux from 'reflux';

const buc_coords = {
  lat: 44.439663,
  lng: 26.096306
};

let initial_zoom = 7;

const Harta = React.createClass({

	mixins: [
		Reflux.connect(MapStore, 'mapstore')
	],
	
	markers: [],
	n_markers: 0,
	
	add_marker(objective) {
		let infowindow = new window.google.maps.InfoWindow({
      content: objective.name
    });
    let i = this.n_markers;
		this.markers[this.n_markers] = new window.google.maps.Marker({
			position: {lat: objective.location.x, lng: objective.location.y},
			map: window.map
		});
		this.markers[this.n_markers].addListener('mouseover', () => {
      infowindow.open(window.map, this.markers[i]);
    });
    this.markers[this.n_markers].addListener('mouseout', () => {
    	infowindow.close();
    });
    this.markers[this.n_markers].addListener('hover', () => {
    	window.map.setCenter({lat: objective.location.x, lng: objective.location.y});
    	window.map.setZoom(15);
    });
    this.markers[this.n_markers].addListener('click', () => {
    	ModalActions.open({
    		type: 'objective',
    		objective: objective
    	});
    });
    this.n_markers = this.n_markers+1;
	},
	
	place_markers() {
		for (let marker_index in this.state.mapstore.data) {
			let objective = this.state.mapstore.data[marker_index];
			this.add_marker(objective);
		}
	},

	get_markers() {
		
		this.place_markers();
		
	},

	init_map() {
		if (window.google == null) {
			setTimeout(this.init_map, 100);
		} else {
			window.map = new window.google.maps.Map(document.getElementById('map'), {
		  	zoom: initial_zoom,
		  	center: buc_coords,
		  	disableDefaultUI: true
			});
			
		}
	},
	
	re_init_map() {
		for (let i in this.markers) {
			if (this.state.mapstore.data[i].visible) {
				this.markers[i].setMap(window.map);
			} else {
				this.markers[i].setMap(null);
			}
		}
	},

	componentDidMount() {
		window.hasMarkers = false;
		this.init_map();
	},
	
	componentDidUpdate() {
		if (!window.hasMarkers && this.state.mapstore.data != []) {
			this.get_markers();
			window.hasMarkers = true;
		}
		this.re_init_map();
	},

	render() {
	
		return <div id='harta' className='col-lg-10 col-md-9 col-sm-9'>
		
			<div id='map' style={{height: '100vh'}}>
			</div>

		</div>;
	}

});

export default Harta;




